import uniBadge from "@/components/uni/uni-badge/uni-badge.vue"
import XcxAuth from "@/components/wx_auth/xcx_auth.vue"
import listCell from '@/components/mix-list-cell';
import {
	WxToken
} from '@/common/wx_token.js'
var wxtoken = new WxToken();
import {
	mapState
} from 'vuex';
let startY = 0,
	moveY = 0,
	pageAtTop = true;
export default {
	components: {
		listCell,
		XcxAuth,
		uniBadge
	},
	data() {
		return {
			list: '', 
			auth: {
				is_name: false,
				is_address: false,
				is_phone: false,
			},
			coverTransform: 'translateY(0px)',
			coverTransition: '0s',
			moving: false,
			userinfo: ''
		}
	},
	onLoad() {

		this.auth.is_name = !this.auth.is_name












	},
	onShow(){			
		this.get_number()
	},




















	computed: {
		...mapState(['hasLogin', 'userInfo'])
	},
	methods: {
		get_number() {
			this.$api.http.post('order/user/order_date').then(res => {
				this.list = res.data
			})
		},
		get_userinfo(e) {
			this.userinfo = e
		},			
		jump_tohelp() {
			uni.navigateTo({
				url: '/pages/list/list'
			})
		},
		jump_toorder(e) {
			uni.navigateTo({
				url: '/pages/order/order?state=' + e
			})
		},
		jump_toset() {
			uni.navigateTo({
				url: '/pages/set/set'
			})
		},
		jump_toaddress() {
			uni.navigateTo({
				url: '/pages/address/address'
			})
		},
		jump_tolike() {
			uni.navigateTo({
				url: '/pages/user/like/like'
			})
		},
		jump_tomycoupon() {
			uni.navigateTo({
				url: '/pages/user/mycoupon/mycoupon'
			})
		},
		jump_toreseller() {
			uni.navigateTo({
				url: '/pages/user/reseller/reseller'
			})
		},
		jump_tocms() {
			uni.navigateTo({
				url: '/pages/cms/index/index'
			})
		},
		/**
		 * 统一跳转接口,拦截未登录路由
		 * navigator标签现在默认没有转场动画，所以用view
		 */
		navTo(url) {
			if (!this.hasLogin) {
				// url = '/pages/public/login';
				uni.navigateTo({
					url
				})
			}
			uni.navigateTo({
				url
			})
		},

		/**
		 *  会员卡下拉和回弹
		 *  1.关闭bounce避免ios端下拉冲突
		 *  2.由于touchmove事件的缺陷（以前做小程序就遇到，比如20跳到40，h5反而好很多），下拉的时候会有掉帧的感觉
		 *    transition设置0.1秒延迟，让css来过渡这段空窗期
		 *  3.回弹效果可修改曲线值来调整效果，推荐一个好用的bezier生成工具 http://cubic-bezier.com/
		 */
		coverTouchstart(e) {
			if (pageAtTop === false) {
				return;
			}
			this.coverTransition = 'transform .1s linear';
			startY = e.touches[0].clientY;
		},
		coverTouchmove(e) {
			moveY = e.touches[0].clientY;
			let moveDistance = moveY - startY;
			if (moveDistance < 0) {
				this.moving = false;
				return;
			}
			this.moving = true;
			if (moveDistance >= 80 && moveDistance < 100) {
				moveDistance = 80;
			}

			if (moveDistance > 0 && moveDistance <= 80) {
				this.coverTransform = `translateY(${moveDistance}px)`;
			}
		},
		coverTouchend() {
			if (this.moving === false) {
				return;
			}
			this.moving = false;
			this.coverTransition = 'transform 0.3s cubic-bezier(.21,1.93,.53,.64)';
			this.coverTransform = 'translateY(0px)';
		}
	},
	onPullDownRefresh() {
		setTimeout(function() {
			uni.stopPullDownRefresh();
		}, 2000);
	}
}
